import React from "react";
import SubmitButton from "../components/autorisation/submitButton";
import TextField from "../components/autorisation/textField";
import FormHeader from "../components/autorisation/formHeader";
import * as yup from "yup";

const EnterForm = () => {
  const [data, setData] = React.useState({
    login: "",
    password: "",
  });
  const [errors, setErrors] = React.useState({});
  const hasError = Object.keys(errors).length;

  const validateScheme = yup.object().shape({
    password: yup
      .string()
      .required("   ")
      .min(4, "   - 4")
      .max(10, "   - 10")
      .matches(
        /[A-Z]+/g,
        "       "
      )
      .matches(/[0-9]+/g, "      "),
    login: yup
      .string()
      .required("   ")
      .min(4, "   - 4")
      .max(10, "   - 10"),
  });

  React.useEffect(() => {
    validate();
  }, [data]);

  const handleChange = (e) => {
    setData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const validate = () => {
    validateScheme
      .validate(data)
      .then(() => setErrors({}))
      .catch((err) => setErrors({ [err.path]: err.message }));
    return Object.keys(errors).length;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const isValid = validate();
    if (isValid) return;
    console.log(data);
  };
  return (
    <>
      <form onSubmit={handleSubmit} className="autorisation">
        <FormHeader />
        <TextField
          handleChange={handleChange}
          value={data.login}
          type="text"
          name="login"
          label="  "
          error={errors.login}
        />
        <TextField
          handleChange={handleChange}
          value={data.password}
          type="password"
          name="password"
          label="  "
          error={errors.password}
        />
        <SubmitButton disabled={hasError} type="submit" text="" />
      </form>
    </>
  );
};

export default EnterForm;
